* {
  margin: 0;
  padding: 0;

  --primary-color: #6c63ff;
  --secondary-color: #5f5f5f;
  --muted-color: #b4b4b4;

  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
}

section {
  width: 100vw;
  padding: 2rem;
  max-width: 1080px;
  margin: 0 auto;
}

hr {
  background-color: var(--muted-color);
  margin: 2rem 0;
}
