[class$="__content-wrapper"] {
  display: grid;
  /* grid-template-columns: 3fr 7fr; */
  grid-template-columns: 1fr;
  grid-template-rows: auto auto;
  /* grid-template-rows: 1fr; */
}

[class$="__image"] {
  width: 100%;
  margin-bottom: 2rem;
}

[class$="__image"] img {
  width: 100%;
}

[class$="__content-wrapper"] [class$="__text"] h2 {
  color: var(--primary-color);
  line-height: 120%;
  font-size: 2rem;
  font-weight: bold;
}
[class$="__content-wrapper"] [class$="__text"] p {
  color: var(--secondary-color);
  line-height: 150%;
  font-size: 1.3rem;
}
[class$="__content-wrapper"] [class$="__text"] span {
  color: var(--primary-color);
  text-decoration: underline;
}
